body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  width: 90%;
  margin: 0 auto;
}
.Header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}
#meme {
  margin: 20px 0 10px;
}
.entry-row {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
}

.entry-col {
  flex: 1;
}
.entry-col-2 {
  flex: 2;
}
.entry-btn-col {
  width: 22px;
}

.entry-input {
  display: block;
  width: 90%;
  border: none;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
}
.entry-input:focus {
  outline: none;
}

@media screen and (min-width: 800px) {
  .App {
    width: 800px;
  }
}

.loss {
  color: #ff333a;
}

.gain {
  color: #093;
}
.bold {
  font-weight: bold;
}
